.header {
  width: 100%;
  z-index: 20;
  @include r(991) {
    position: fixed;
    left: 0;
    right: 0;
    width: auto; }
  &__logo {
    @include r(991) {
      padding-bottom: 5px; } }
  &__back {
    position: relative;
    z-index: 1;
    @include r(991) {
      background: $color_white; } }
  &__lang {
    @include r(991) {
      margin-right: 20px; } }
  &__row {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $color_white;
    padding-top: 20px;
    z-index: 1; }
  &__burger {
    display: none;
    @include r(991) {
      display: flex;
      justify-content: center;
      align-items: center; } }
  &__functional {
    display: flex; }
  &__box {
    position: relative; }
  &__modal {
    position: absolute;
    width: 100%;
    height: 100vh;
    height: calc(100vh - #{$header_mobile_h + 6 + 'px'} );
    overflow: scroll;
    visibility: hidden;
    transition: visibility 0s linear 0.5s; }
  .modal-open &__modal {
    @include r(991) {
      visibility: visible;
      transition-delay: 0s; } }
  &__modal-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $color_t;
    transform: translate3d(0,-150%,0) skewY(-10deg);
    transition: transform .9s cubic-bezier(.19,1,.22,1), -webkit-transform .9s cubic-bezier(.19,1,.22,1);
    transition-delay: 0.3s; }
  .modal-open &__modal-bg {
    @include r(991) {
      transform: translate3d(0,0,0) skewY(0);
      transition-delay: 0s; } }
  &__modal-body {
    @include vcenter; } }

.lang {
	&__list {
		position: relative;
		width: 40px;
		height: 40px;
		border: 2px solid $color_secondary; }
	&__item {
    font-family: $font_secondary;
    font-weight: 700;
    color: $color_t;
    @include vcenter; } }

.burger {
  cursor: pointer;
  &__ico {
    position: relative;
    display: inline-block;
    width: 15px;
    height: 13px;
    vertical-align: middle;
    margin-right: 5px; }
  &__text {
    font-family: $font_secondary;
    font-weight: 700;
    transition: all 0.3s ease-in-out; }
  &__bar {
    position: absolute;
    height: 3px;
    background-color: #202121;
    overflow: hidden;
    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      background: $color_secondary;
      position: absolute;
      top: 0;
      left: 100%;
      z-index: 1;
      transition: 0.3s; }
    &:nth-child(1) {
      top: 0;
      width: 9px;
      &:after {
        transition-delay: 0.1s; } }
    &:nth-child(2) {
      top: 5px;
      width: 15px;
      &:after {
        transition-delay: 0.2s; } }
    &:nth-child(3) {
      top: 10px;
      width: 12px;
      &:after {
        transition-delay: 0.3s; } } }
  &:hover &__text {
    color: $color_secondary; }
  &:hover &__bar:after {
    left: 0; } }

.search-btn {
  margin-right: 20px; }
