.news {
  position: relative;
  background: #f5f5f5;
  margin-top: -25px;
  z-index: 1;
  .post__text {
    transform: translateX(-100%);
    transition: all .3s;
    transition-delay: .1s; }
  .post__title {
    transform: translateX(-100%);
    transition: all .3s;
    transition-delay: .2s; }
  &__blocks {
    display: flex;
    flex-wrap: wrap; }
  &__col {
    flex: 0 0 50%; }
  &__item {
    position: relative;
    &:hover {
      .news__content {
        opacity: 1; }
      .post__text, .post__title {
        transform: translateX(0); } } }
  &__content {
    padding: 35px;
    background: $color_secondary;
    opacity: 0;
    overflow: hidden;
    transition: opacity .3s; }
  &__background {
    display: inline-block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-position: 50% 50%;
    background-repeat: none;
    background-size: cover;
    z-index: -1; }
  &__footer {
    margin-top: 120px; } }
