.icon-logo {
	fill: $color_secondary;
	width: 180px;
	height: 74px;
	@include r(1199) {
		width: 140px;
		height: 57px; }
	@include r(991) {
		width: 127px;
		height: 52px; } }

.scroll-down {
	@include vcenter_gor_inline;
	bottom: 7px; }

.read-more {
	font-size: 20px;
	font-family: $font_secondary;
	font-weight: 400;
	line-height: 1.1;
	text-transform: uppercase;
	color: $color_t;
	text-decoration: none; }

.row {
	display: flex;
	margin: 0 -15px; }

.col-1-4 {
	flex: 0 0 25%;
	padding: 0 15px; }

.no-btn {
	@include reset_btn; }

#scroll-up {
	position: fixed;
	right: 50px;
	bottom: 50px;
	background: $color_white;
	width: 40px;
	height: 40px;
	transform: translateZ(0) scale(0);
	transition: transform .3s;
	&.show {
		transform: translateZ(0) scale(1); }
	z-index: 10; }

.arrow-up {
	display: inline-block;
	width: 15px;
	height: 15px;
	transform: rotate(-135deg);
	border-left: none;
	border-top: none;
	border-right: 2px $color_t solid;
	border-bottom: 2px $color_t solid;
	transform: rotate(-135deg) translate(-20%, -20%); }

.sq {
	font-size: 0;
	&__box {
		position: relative;
		display: inline-block;
		width: 40px;
		height: 40px;
		border: 2px solid $color_secondary;
		transition: background .3s cubic-bezier(.19,1,.22,1); }
	&:hover &__box {
		background: $color_secondary; }
	&__rotate180 {
		transform: rotate(180deg); }
	&__vcenter {
		font-size: 0;
		@include vcenter; }
	svg {
		font-size: 20px; }
	&:hover svg {
		fill: $color_white; }
	&-group {
		.sq .sq__box {
			border-left-width: 1px;
			border-right-width: 1px; }
		.sq:first-child .sq__box {
			border-left-width: 2px; }
		.sq:last-child .sq__box {
			border-right-width: 2px; } } }
