html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-arrow-down {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-decor {
  width: 1.54em;
  height: 1em;
  fill: #00AAEF;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 2.42em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

.nav .nav__link {
  display: inline-block;
  position: relative;
  overflow: hidden;
  padding: 2px 20px;
  line-height: 1;
}

@media only screen and (max-width: 1199px) {
  .nav .nav__link {
    padding: 1px 15px;
  }
}

.nav .nav__link:after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  z-index: 0;
  background: #1bb4d0;
  transform: translate3d(102%, 0, 0);
  transition: transform 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.nav .nav__link:hover::after {
  transform: translate3d(0, 0, 0);
}

.nav .nav__link:hover .nav__t:before {
  width: 100%;
}

.nav .nav__t {
  display: block;
  position: relative;
  z-index: 1;
}

.nav .nav__t:before {
  content: attr(data-letters);
  position: absolute;
  color: #ffffff;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
  width: 0%;
  transition: width 0.5s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

@font-face {
  font-family: "affect";
  src: url("../fonts/affect-webfont.woff2") format("woff2"), url("../fonts/affect-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "affect";
  src: url("../fonts/affect_light-webfont.woff2") format("woff2"), url("../fonts/affect_light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "affect";
  src: url("../fonts/affect_bold-webfont.woff2") format("woff2"), url("../fonts/affect_bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: #FFF;
  font-family: "Open Sans",sans-serif;
  font-weight: 300;
  font-size: 17px;
  line-height: 1.4;
  color: #29262d;
}

p {
  margin-bottom: 15px;
}

h2 {
  font-family: "affect", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 38px;
  line-height: 1.2;
}

h3 {
  font-family: "affect", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 30px;
  line-height: 1.2;
}

.container {
  margin: 0 auto;
  max-width: 1920px;
  padding: 0 5%;
}

.container--h {
  height: 100%;
}

.container-inner {
  padding: 0 13.65%;
}

.modal-container {
  max-width: 1920px;
  margin: 0 5%;
}

.icon-logo {
  fill: #1bb4d0;
  width: 180px;
  height: 74px;
}

@media only screen and (max-width: 1199px) {
  .icon-logo {
    width: 140px;
    height: 57px;
  }
}

@media only screen and (max-width: 991px) {
  .icon-logo {
    width: 127px;
    height: 52px;
  }
}

.scroll-down {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 7px;
}

.read-more {
  font-size: 20px;
  font-family: "affect", sans-serif;
  font-weight: 400;
  line-height: 1.1;
  text-transform: uppercase;
  color: #29262d;
  text-decoration: none;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.col-1-4 {
  flex: 0 0 25%;
  padding: 0 15px;
}

.no-btn {
  background: none;
  border: 0;
  color: inherit;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  appearance: none;
  user-select: none;
}

#scroll-up {
  position: fixed;
  right: 50px;
  bottom: 50px;
  background: #ffffff;
  width: 40px;
  height: 40px;
  transform: translateZ(0) scale(0);
  transition: transform .3s;
  z-index: 10;
}

#scroll-up.show {
  transform: translateZ(0) scale(1);
}

.arrow-up {
  display: inline-block;
  width: 15px;
  height: 15px;
  transform: rotate(-135deg);
  border-left: none;
  border-top: none;
  border-right: 2px #29262d solid;
  border-bottom: 2px #29262d solid;
  transform: rotate(-135deg) translate(-20%, -20%);
}

.sq {
  font-size: 0;
}

.sq__box {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 2px solid #1bb4d0;
  transition: background 0.3s cubic-bezier(0.19, 1, 0.22, 1);
}

.sq:hover .sq__box {
  background: #1bb4d0;
}

.sq__rotate180 {
  transform: rotate(180deg);
}

.sq__vcenter {
  font-size: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sq svg {
  font-size: 20px;
}

.sq:hover svg {
  fill: #ffffff;
}

.sq-group .sq .sq__box {
  border-left-width: 1px;
  border-right-width: 1px;
}

.sq-group .sq:first-child .sq__box {
  border-left-width: 2px;
}

.sq-group .sq:last-child .sq__box {
  border-right-width: 2px;
}

.sc {
  padding: 60px 0;
}

.sc__heading {
  position: relative;
  padding: 60px 0;
  line-height: 1;
}

.sc__heading:before {
  content: '';
  position: relative;
  width: 3px;
  height: 0.84em;
  background: #1bb4d0;
  display: inline-block;
  vertical-align: middle;
  margin-right: 40px;
  margin-left: -44px;
}

.sc__heading--bwhite:before {
  background: #ffffff;
}

.header {
  width: 100%;
  z-index: 20;
}

@media only screen and (max-width: 991px) {
  .header {
    position: fixed;
    left: 0;
    right: 0;
    width: auto;
  }
}

@media only screen and (max-width: 991px) {
  .header__logo {
    padding-bottom: 5px;
  }
}

.header__back {
  position: relative;
  z-index: 1;
}

@media only screen and (max-width: 991px) {
  .header__back {
    background: #ffffff;
  }
}

@media only screen and (max-width: 991px) {
  .header__lang {
    margin-right: 20px;
  }
}

.header__row {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  padding-top: 20px;
  z-index: 1;
}

.header__burger {
  display: none;
}

@media only screen and (max-width: 991px) {
  .header__burger {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.header__functional {
  display: flex;
}

.header__box {
  position: relative;
}

.header__modal {
  position: absolute;
  width: 100%;
  height: 100vh;
  height: calc(100vh - 88px);
  overflow: scroll;
  visibility: hidden;
  transition: visibility 0s linear 0.5s;
}

@media only screen and (max-width: 991px) {
  .modal-open .header__modal {
    visibility: visible;
    transition-delay: 0s;
  }
}

.header__modal-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #29262d;
  transform: translate3d(0, -150%, 0) skewY(-10deg);
  transition: transform 0.9s cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform 0.9s cubic-bezier(0.19, 1, 0.22, 1);
  transition-delay: 0.3s;
}

@media only screen and (max-width: 991px) {
  .modal-open .header__modal-bg {
    transform: translate3d(0, 0, 0) skewY(0);
    transition-delay: 0s;
  }
}

.header__modal-body {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lang__list {
  position: relative;
  width: 40px;
  height: 40px;
  border: 2px solid #1bb4d0;
}

.lang__item {
  font-family: "affect", sans-serif;
  font-weight: 700;
  color: #29262d;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.burger {
  cursor: pointer;
}

.burger__ico {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 13px;
  vertical-align: middle;
  margin-right: 5px;
}

.burger__text {
  font-family: "affect", sans-serif;
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}

.burger__bar {
  position: absolute;
  height: 3px;
  background-color: #202121;
  overflow: hidden;
}

.burger__bar:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: #1bb4d0;
  position: absolute;
  top: 0;
  left: 100%;
  z-index: 1;
  transition: 0.3s;
}

.burger__bar:nth-child(1) {
  top: 0;
  width: 9px;
}

.burger__bar:nth-child(1):after {
  transition-delay: 0.1s;
}

.burger__bar:nth-child(2) {
  top: 5px;
  width: 15px;
}

.burger__bar:nth-child(2):after {
  transition-delay: 0.2s;
}

.burger__bar:nth-child(3) {
  top: 10px;
  width: 12px;
}

.burger__bar:nth-child(3):after {
  transition-delay: 0.3s;
}

.burger:hover .burger__text {
  color: #1bb4d0;
}

.burger:hover .burger__bar:after {
  left: 0;
}

.search-btn {
  margin-right: 20px;
}

.footer {
  font-size: 15px;
}

.footer__location {
  font-size: 18px;
  font-family: "affect", sans-serif;
  font-weight: 700;
  color: #1bb4d0;
  margin-bottom: 20px;
}

.nav {
  font-family: "affect", sans-serif;
  font-weight: 700;
  font-size: 0;
}

.nav__item {
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  z-index: 1;
}

.nav__item:last-child .nav__link {
  border-right: none;
}

.nav__link {
  font-size: 18px;
  text-decoration: none;
  color: #29262d;
  border-right: 2px solid #1bb4d0;
  text-decoration: none;
}

@media only screen and (max-width: 1199px) {
  .nav__link {
    font-size: 16px;
  }
}

.nav__link:hover {
  color: #1bb4d0;
}

@media only screen and (max-width: 991px) {
  .nav {
    display: none;
  }
}

.footer__nav {
  font-size: 15px;
}

.footer__nav .nav__item {
  display: block;
}

.footer__nav .nav__link {
  border-right: none;
  padding: 0;
}

.footer__nav .nav__link:after {
  display: none;
}

.m-nav__item {
  overflow: hidden;
  transform: translateZ(0);
}

@media only screen and (max-width: 991px) {
  .modal-open .m-nav__link {
    transform: translate(0%, 0%);
    transition-delay: .4s, 0s;
  }
}

.m-nav__link {
  display: inline-block;
  transform: translate(0%, 100%);
  font-size: 40px;
  font-family: "affect", sans-serif;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  transition: transform 0.4s cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform 0.4s cubic-bezier(0.19, 1, 0.22, 1);
  transition-delay: 0s;
}

.m-nav__link:hover {
  color: #1bb4d0;
}

.welcome {
  position: relative;
  height: calc(100vh - 100px);
  min-height: 500px;
}

@media only screen and (max-width: 1199px) {
  .welcome {
    height: 100vh;
    height: calc(100vh - 82px);
  }
}

@media only screen and (max-width: 991px) {
  .welcome {
    height: 100vh;
    padding-top: 82px;
  }
}

.welcome__container {
  height: 90%;
  padding-top: 15px;
}

.welcome__video {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.welcome__placeholder {
  height: 100%;
  background: #b1b1b1;
}

#bgvid {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: 1000px;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
  transition: 1s opacity;
}

.about {
  font-size: 20.8px;
  line-height: 1.54;
}

.about__links {
  font-size: 24px;
  font-family: "affect", sans-serif;
  font-weight: 400;
  margin: 40px 0;
}

.about__link {
  color: #29262d;
  text-transform: uppercase;
  text-decoration: none;
  transition: color .3s;
}

.about__link:hover {
  color: #1bb4d0;
}

.about__link:after {
  content: '';
  position: relative;
  width: 3px;
  height: 0.84em;
  background: #1bb4d0;
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
}

.about__link:last-child:after {
  display: none;
}

.founder {
  position: relative;
  padding: 0;
  z-index: 2;
}

.founder__row {
  display: flex;
  background: #1bb4d0;
}

@media only screen and (max-width: 1199px) {
  .founder__row {
    display: block;
  }
}

.founder__name {
  margin-bottom: 20px;
}

.founder__col-l {
  padding-bottom: 60px;
}

.founder__col-r {
  font-size: 0;
  text-align: center;
}

.founder__img {
  height: 100%;
  height: calc(100% + 20px);
  transform: translateY(-20px);
}

@media only screen and (max-width: 1199px) {
  .founder__img {
    max-width: 100%;
    height: auto;
    transform: translateY(0);
  }
}

.news {
  position: relative;
  background: #f5f5f5;
  margin-top: -25px;
  z-index: 1;
}

.news .post__text {
  transform: translateX(-100%);
  transition: all .3s;
  transition-delay: .1s;
}

.news .post__title {
  transform: translateX(-100%);
  transition: all .3s;
  transition-delay: .2s;
}

.news__blocks {
  display: flex;
  flex-wrap: wrap;
}

.news__col {
  flex: 0 0 50%;
}

.news__item {
  position: relative;
}

.news__item:hover .news__content {
  opacity: 1;
}

.news__item:hover .post__text, .news__item:hover .post__title {
  transform: translateX(0);
}

.news__content {
  padding: 35px;
  background: #1bb4d0;
  opacity: 0;
  overflow: hidden;
  transition: opacity .3s;
}

.news__background {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
  background-repeat: none;
  background-size: cover;
  z-index: -1;
}

.news__footer {
  margin-top: 120px;
}
