@include font('affect', affect-webfont);
@include font('affect', affect_light-webfont, 300);
@include font('affect', affect_bold-webfont, 700);

html {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

body {
	background: #FFF;
	font-family: "Open Sans",sans-serif;
	font-weight: 300;
	font-size: 17px;
	line-height: 1.4;
	color: $color_t; }

p {
	margin-bottom: 15px; }

h2 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 38px;
	line-height: 1.2; }
h3 {
	font-family: $font_secondary;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 30px;
	line-height: 1.2; }

.container {
	margin: 0 auto;
	max-width: 1920px;
	padding: 0 5%;
	&--h {
		height: 100%; }
	&-inner {
		padding: 0 13.65%; } }

.modal-container {
	max-width: 1920px;
	margin: 0 5%; }

